.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App-header {
  animation: fadeIn 2s ease-in;
}

h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 10px rgba(0,0,0,0.4);
}

p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.buttons a {
  text-decoration: none;
  color: white;
  border: 2px solid white;
  padding: 0.5rem 1.5rem;
  margin: 0 1rem;
  border-radius: 25px;
  transition: 0.3s;
  display: inline-flex;
  align-items: center;
}

.buttons a:hover {
  background: white;
  color: #ff4b2b;
  transform: scale(1.1);
}

.logo {
  height: 20px;
  margin-right: 0.5rem;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 1rem 0;
  background: rgba(0,0,0,0.2);
  font-size: 0.9rem;
}

@keyframes fadeIn {
  from {opacity: 0; transform: translateY(-20px);}
  to {opacity: 1; transform: translateY(0);}
}
